
.shopcart{
  position:fixed;
  left:0;
  bottom:0;
  z-index:50;
  width:100%;
  height:48px;
  background:#000;
}
.shopcart>.content{
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  background:#141d27
}
.shopcart>.content>.content-left{
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
}
.shopcart>.content>.content-left>.logo-wrapper{
  display:inline-block;
  position:relative;
  top:-10px;
  margin:0 12px;
  padding:6px;
  width:56px;
  height:56px;
  box-sizing:border-box;
  vertical-align:top;
  border-radius:50%;
  background:#141d27;
}
.shopcart>.content>.content-left>.logo-wrapper>.logo{
  width:100%;
  height:100%;
  border-radius:50%;
  background:#242b3c;
  text-align:center;
}
.shopcart>.content>.content-left>.logo-wrapper>.logo.highlight{
  background:rgb(0,160,220);
}
.shopcart>.content>.content-left>.logo-wrapper>.logo>.icon-shopping_cart{
  font-size:25px;
  color:#80858a;
  line-height:44px;
}
.shopcart .content .content-left .logo-wrapper .logo.highlight>.icon-shopping_cart{
  color:#fff;
}
.shopcart .content .content-left .price{
    display:inline-block;
    vertical-align:top;
    margin-top:12px;
    line-height:24px;
    box-sizing:border-box;
    padding-right:12px;
    border-right:1px solid rgba(255,255,255,0.1);
    font-size:16px;
    font-weight:700;
    color:rgba(255,255,255,0.4);
}
.shopcart .content .content-left .price.highlight{
    color:#fff;
}
.shopcart .content .content-left .desc{
      display:inline-block;
      vertical-align:top;
      line-height:24px;
      margin:12px 0 0 12px;
      color:rgba(255,255,255,0.4);
      font-size:10px;
}
.shopcart .content .content-right{
  -webkit-box-flex:0;
      -ms-flex:0 0 105px;
          flex:0 0 105px;
  width:105px;
}
.shopcart .content .content-right .pay{
  height:48px;
  line-height:48px;
  text-align:center;
  font-size:12px;
  color:rgba(255,255,255,0.4);
  font-weight:700;
  background:#2b333b;
}
.shopcart .content .content-right .pay.not-enough{
  background:#2b333b;
}
.shopcart .content .content-right .pay.enough{
  background:#00b43c;
  color:#fff;
}
.shopcart .content .content-left .logo-wrapper .num{
  position:absolute;
  top:0;
  right:0;
  width:24px;
  height:16px;
  line-height:16px;
  text-align:center;
  border-radius:16px;
  font-size:9px;
  font-weight:700;
  background:rgb(240,20,20);
  color:#fff;
  box-shadw:0 4px 8px 0 rgba(0,0,0,0.4);
}
.shopcart-list{
  position:absolute;
  bottom:46px;
  left:0;
  z-index:-1;
  width:100%;
  -webkit-transform:translated3d(0,-100%,0);
          transform:translated3d(0,-100%,0);
  transition:all 0.5s;
}
.shopcart-list.folds-enter-to,.shopcart-list.folds-leave{
  -webkit-transform:translated3d(0,0,0);
          transform:translated3d(0,0,0);
}
.list-header{
  height:40px;
  line-height:30px;
  padding:0 18px;
  padding-top:10px;
  background:rgb(230,230,230);
  border-bottom:1px solid rgba(120,220,220,0.6);
}
.list-content{
  padding:0 18px;
  max-height:217px;
  background:#fff;
  width:90%;
  overflow:hidden;
}
.list-content .food{
  position:relative;
  padding:12px 0;
  box-sizing:border-box;
  border:1px solid rgba(7,17,27,0.1)
}
.list-content .food .name{
  line-height:24px;
  font-size:14px;
  color:rgb(7,17,27);
}
.list-content .food .price{
  position:absolute;
  right:98px;
  bottom:12px;
  line-height:24px;
  font-size:14px;
  font-weight:700;
  color:rgb(240,20,20);
}
.list-content .food .cartcontrol-wrapper{
  position:absolute;
  right:0;
  bottom:16px;
}
.list-header .title{
  float:left;
  font-size:14px;
  color:rgb(7,17,27);
}
.list-header .empty{
  float:right;
  font-size:12px;
  color:rgb(0,160,220);
}
.list-mask{
  position: fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  z-index:40;
  opacity:1;
  transition: all 0.5s;
  -webkit-backdrop-filter:blur(10px);
          backdrop-filter:blur(10px);
  background:rgba(7,17,27,0.6);
}




