
body,html{
  line-height:1;
  font-weight:200;
  font-family:'PingFang SC','STHeitiSC-Light','Helvetica-Light',arial,sans-serif;
}
.clearfix{
  display:inline-block;
}
.clearfix:after{
  display:block;
  content:'.';
  height:0;
  line-height:0;
  clear:both;
  visibility:hidden;
}
.header{
  color: #fff;
  position:relative;
  overflow:hidden;
  background:rgba(7,17,27,0.3);
}
.header>.content-wrapper{
  padding:24px 12px 18px 24px;
  font-size:0;
  position:relative;
}
.header>.content-wrapper>.avatar{
  display: inline-block;
  vertical-align:top;
}
.avatar>img{
  border-radius:2px;
}
.header>.content-wrapper>.content{
  display:inline-block;
  margin-left:16px;
  font-size:14px;
}
.title{
  margin:2px 0 8px 0;
}
.brand{
  display:inline-block;
  width:30px;
  height:18px;
  background-image:url("brand@2x.png");
  background-size:30px;
  background-repeat:no-repeat;
  vertical-align:top;
}
.name{
  margin-left:6px;
  font-size:16px;
  line-height:18px;
  font-weight:bold;
}
.star-wrapper{
  margin-top:18px;
  padding:2px 0;
  text-align:center;
}
.description {
  margin-bottom:10px;
  line-height:12px;
  font-size:12px;
}
.support>.icon{
  display:inline-block;
  width:12px;
  height:12px;
  margin-right:4px;
  background-size:12px;
  background-repeat:no-repeat;
  vertical-align:top;
}
.decrease{
  background-image:url('decrease_1@2x.png');
}
.discount{
  background-image:url('discount_1@2x.png');
}
.guarantee{
  background-image:url('guarantee_1@2x.png');
}
.invoice{
  background-image:url('invoice_1@2x.png');
}
.special{
  background-image:url('special_1@2x.png');
}
.text{
  font-size:12px;
  line-height:12px;
}
.support-count{
  position:absolute;
  right:12px;
  bottom:18px;
  padding:0 8px;
  height:24px;
  line-height:24px;
  border-radius:14px;
  background:rgba(0,0,0,0.2);
  text-align:center;
}
.count{
  font-size:10px;
}
.icon-keyboard_arrow_right{
  font-size:10px;
  vertical-align:top;
  line-height:24px;
  margin-left:2px;
}
.bulletin-wrapper{
  height:28px;
  line-height:28px;
  padding:0 22px 0 12px;
  white-space: nowrap;
  overflow:hidden;
  text-overflow:ellipsis;
  position:relative;
  background:rgba(7,17,27,0.2);
}
.bulletin-title{
  display:inline-block;
  vertical-align:top;
  margin-top:8px;
  width:22px;
  height:12px;
  background-image:url('bulletin@2x.png');
  background-size:22px 12px;
  background-repeat:no-repeat;
}
.bulletin-text{
  vertical-align:top;
  font-size:10px;
  margin:0 4px;
}
.icon-arrow_right{
  position:absolute;
  right:12px;
  font-size:10px;
}
.background{
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  z-index:-1;
  -webkit-filter:blur(10px);
          filter:blur(10px);
}
.detail{
  position:fixed;
  z-index:100;
  top:0;
  left:0;
  width:100%;
  height:100%;
  overflow:auto;
  transition:all 0.5s;
  -webkit-backdrop-filter:blur(10px);
          backdrop-filter:blur(10px);
  opacity:1;
      background:rgba(7,17,27,0.8);
}
.fade-enter,.fade-leave{
  opacity:0;
  background:rgba(7,17,27,0);
}
.detail-wrapper{
  min-height:100%;
  width:100%;
}
.detail-main{
  margin-top:64px;
  padding-bottom:64px;
}
.detail-close{
  position:relative;
  width:32px;
  height:32px;
  margin:-64px auto;
  clear:both;
  font-size:32px;
}
.detail-main>.name{
  line-height:16px;
  text-align:center;
  font-size:16px;
  font-weight:700;
}
.detail-main>.title{
  width:80%;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  margin:28px auto 24px auto;
}
.detail-main>.title>.line{
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  position:relative;
  top:-6px;
  border-bottom:1px solid rgba(255,255,255,0.2);
}
.detail-main>.title>.text{
  padding:0 12px;
  font-size:14px;
  font-weight:700;
}
.detail-main>.supports{
  width:80%;
  margin:0 auto;
}
.detail-main>.supports>.support-item{
  padding:0 12px;
  margin-bottom:12px;
  font-size:0;
}
.detail-main>.supports>.support-item:last-child{
  margin-bottom:0;
}
.detail-main>.supports>.support-item>.icon{
  display:inline-block;
  width:16px;
  height:16px;
  vertical-align:top;
  margin-right:6px;
  background-size:16px;
  background-repeat:no-repeat;
}
.detail-main>.supports>.support-item>.text{
  line-height:16px;
  font-size:12px;
}
.detail-main>.bulletin{
  width:80%;
  margin:0 auto;
}
.detail-main>.bulletin>.content{
  padding:0 12px;
  line-height:24px;
  font-size:12px;
}



