
.ratingselect{
}
.ratingselect .rating-type{
  padding:18px 0;
  margin:0 18px;
  border-bottom:1px solid rgba(7,17,27,0.1);
}
.ratingselect .rating-type .block{
  display:inline-block;
  padding:8px 12px;
  margin-right:8px;
  border-radius: 1px ;
  color:rgb(77,85,93);
  font-size:12px;
  line-height:16px;
}
.ratingselect .rating-type .block.active{
  color:#fff;
}
.ratingselect .rating-type .count{
  font-size:8px;
  margin-left:2px;
}
.ratingselect .rating-type .positive{
  background:rgba(0,160,220,0.2);
}
.ratingselect .rating-type .positive.active{
  background:rgb(0,160,220);
}
.ratingselect .rating-type .negative{
  background:rgba(77,85,93,0.2);
}
.ratingselect .rating-type .negative.active{
  background:rgb(77,85,93);
}
.ratingselect .switch{
  padding:12px 18px;
  line-height:24px;
  border-bottom:1px solid rgba(7,17,27,0.1);
  color:rgb(147,153,159);
}
.ratingselect .switch .icon-check_circle{
  font-size:24px;
  margin-right:4px;
  border-radius:50%;
  width:24px;
  height:12px;
  display:inline-block;
  background:#999;
  color:#fff;
  text-align:center;
  line-height:24px;
  padding:6px 0;
  vertical-align:top;
}
.ratingselect .switch.on .icon-check_circle{
  background:#00c850;
  color:#fff;
}
.ratingselect .switch .text{
  font-size:12px;
  line-height:12px;
}



