
.cartcontrol .cart-decrease{
    display:inline-block;
    padding:6px;
}
.cartcontrol .cart-decrease>.inner{
    display:inline-block;
    font-size:24px;
    color: rgb(0,160,220);
}
.cartcontrol .cart-count{
    display:inline-block;
}
.cartcontrol .cart-add{
    display:inline-block;
    padding:3px;
    width:10px;
    height:10px;
    line-height:10px;
    font-size:16px;
    border-radius:50%;
    color:#fff;
    background: rgb(0,160,220);
}
.cartcontrol .icon-remove_circle_outline{
    border:1px solid rgb(0,160,220);
    background:#fff;
    width:8px;
    height:8px;
    display:inline-block;
    padding:3px;
    border-radius:50%;
    margin-top:1px;
    line-height:6px;
    color:rgb(0,160,220);
}
.cart-count{
    display:inline-block;
    vertical-align:top;
    width:12px;
    padding-top:6px;
    line-height:6px;
    text-align:center;
    font-size:10px;
    color:rgb(147,153,139);
}



