
.goods{
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  position:absolute;
  top:174px;
  bottom:46px;
  width:100%;
  overflow:hidden;
}
.goods>.menu-wrapper{
  -webkit-box-flex:0;
      -ms-flex:0 0 80px;
          flex:0 0 80px;
  width:80px;
  background:#f3f5f7;
}
.goods>.menu-wrapper .menu-item{
  display:table;
  height:54px;
  width:56px;
  line-height:14px;
  padding:0 12px;
  border-bottom:1px solid rgba(7,17,27,0.2);
}
.goods>.menu-wrapper .menu-item.current{
  position:relative;
  z-index:10;
  margin-top:-1px;
  background:#fff;
  font-weight:700
}
.goods>.menu-wrapper .menu-item .icon{
  display:inline-block;
  width:12px;
  height:12px;
  margin-right:2px;
  background-size:12px;
  background-repeat:no-repeat;
  vertical-align:top;
}
.decrease{
  background-image:url('../header/decrease_1@2x.png');
}
.discount{
  background-image:url('../header/discount_1@2x.png');
}
.guarantee{
  background-image:url('../header/guarantee_1@2x.png');
}
.invoice{
  background-image:url('../header/invoice_1@2x.png');
}
.special{
  background-image:url('../header/special_1@2x.png');
}
.goods>.menu-wrapper .menu-item .text{
  display:table-cell;
  vertical-align:middle;
  width:56px;
  font-size:14px;
}
.goods>.foods-wrapper{
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
}
.goods>.foods-wrapper .title{
  padding-left:14px;
  height:26px;
  line-height:26px;
  border-left:2px solid #d9dde1;
  font-size:12px;
  color:rgb(14,153,159);
  background:#f3f5f7;
}
.goods>.foods-wrapper .food-item{
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  margin:18px;
  padding-bottom:18px;
  border-bottom:1px solid rgba(7,17,17,0.1);
  position:relative;
}
.goods>.foods-wrapper .food-item:last-child{
  border-bottom:none;
  margin-bottom:0;
}
.goods>.foods-wrapper .icon{
  -webkit-box-flex:0;
      -ms-flex:0 0 57px;
          flex:0 0 57px;
  margin-right:10px;
}
.goods>.foods-wrapper .content{
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
}
.goods>.foods-wrapper .content .name{
  margin:2px 0 8px 0;
  height:14px;
  line-height:14px;
  font-size:14px;
  color:rgb(7,17,27);
}
.goods>.foods-wrapper .content .desc{
  line-height:10px;
  margin-bottom:8px;
  font-size:10px;
  color:rgb(147,153,159);
}
.goods>.foods-wrapper .content .extra{
  line-height:10px;
  font-size:10px;
  color:rgb(147,153,159);
}
.goods>.foods-wrapper .content .count{
  margin-right:12px;
}
.goods>.foods-wrapper .content .price{
  font-weight:700;
  font-size:24px;
}
.goods>.foods-wrapper .content .price .now{
  margin-right:8px;
  font-size:14px;
  color:rgb(240,20,20);
}
.goods>.foods-wrapper .content .price .old{
  text-decoration:line-through;
  font-size:10px;
  color:rgb(147,153,159);
}
.cartcontrol-wrapper{
  position:absolute;
  right:0;
  bottom:12px;
}



