
.food{
  position:fixed;
  left:0;
  top:0;
  bottom:48px;
  z-index:30;
  width:100%;
  background:#fff;
}
.image-header{
  position:relative;
  width:100%;
  height:0;
  padding-top:100%;
}
.image-header img{
  position:absolute;
  width:100%;
  height:100%;
  top:0;
  left:0;
}
.image-header .back{
  position:absolute;
  top:10px;
  left:0;
}
.image-header .back .icon-arrow_lift{
  display:block;
  padding:10px;
  font-size:20px;
  color:#fff;
}
.food-content  .content{
  padding:18px;
  position:relative;
}
.food-content  .content .title{
  line-height: 14px;
  margin-bottom:8px;
  font-size:14px;
  font-weight:700px;
  color:rgb(7,17,27);
}
.food-content  .content .details{
  margin-bottom:18px;
  line-height:10px;
  font-size:0 ;
  height:10px;
}
.food-content  .content .details .sell-count{
  color:rgb(147,153,159);
  font-size:10px;
  margin-right:12px;
}
.food-content .content .details .rating{
  color:rgb(147,153,159);
  font-size:10px;
}
.food-content  .content .price{
  font-weight:700;
  font-size:24px;
}
.food-content  .content .price .now{
  margin-right:8px;
  font-size:14px;
  color:rgb(240,20,20);
}
.food-content  .content .price .old{
  text-decoration:line-through;
  font-size:10px;
  color:rgb(147,153,159);
}
.food-content .cartcontrol-wrapper{
  position:absolute;
  right:12px;
  bottom:12px;
}
.food-content .buy{
  position:absolute;
  right:9px;
  bottom:9px;
  z-index:10;
  height:24px;
  line-height:24px;
  padding:0 12px;
  box-sizing:border-box;
  font-size:10px;
  border-radius:12px;
  color:#fff;
  background:rgb(0,160,220);
}
.food-content .info{
  padding:18px;
}
.food-content .info .title{
  margin-bottom:6px;
  font-size:14px;
  line-height:14px;
  color:rgb(7,17,27);
}
.food-content .info .text{
  line-height:24px;
  padding:0 8px;
  font-size:12px;
  color:#4d555d;
}
.rating {
  padding-top:18px;
}
.rating .title{
  margin-left:18px;
  font-size:14px;
  line-height:14px;
  color:rgb(7,17,27);
}
.rating .rating-wrapper{
  padding:0 18px;
}
.rating .rating-wrapper .rating-item{
  position:relative;
  padding:16px 0;
  border-bottom:1px solid rgba(7,17,27,0.1);
}
.rating .rating-wrapper .rating-item .user{
  position:absolute;
  right:0;
  top:16px;
  font-size:0;
  line-height:12px;
}
.rating .rating-wrapper .rating-item .name{
  display:inline-block;
  vertical-align:top;
  font-size:10px;
  margin-right:6px;
  color:rgb(147,153,159);
}
.rating .rating-wrapper .rating-item .avatar{
  border-radius:50%;
}
.rating .rating-wrapper .rating-item .time{
  line-height:12px;
  margin-bottom:6px;
  font-size:10px;
  color:rgb(147,153,159);
}
.rating .rating-wrapper .rating-item .text{
  line-height:16px;
  font-size:12px;
  color:rgb(7,17,27);
}
.rating .rating-wrapper .rating-item .icon-thumb_up,
.rating .rating-wrapper .rating-item .icon-thumb_down{
  line-height:10px;
  margin-right:4px;
  font-size:12px;
  border-radius:50%;
  width:16px;
  height:8px;
  display:inline-block;
  background:rgb(0,160,220);
  color:#fff;
  text-align:center;
  padding:4px 0;
  vertical-align:top;
}
.rating .rating-wrapper .rating-item .icon-thumb_down{
  background:#999;
}
.no-rating {
  padding:16px 0;
  font-size:12px;
  color:rgb(147,153,159);
}



